.itemWrap{
    width: 300px;
    word-break: break-word;
    display: flex;
    flex-direction: column;
    background-color: rgb(59, 45, 41);
    border: 1px solid #131313;
    padding: 10px;
    border-radius: 10px;
    margin: 10px auto;
    
    // color: white;
}
.item{
    // border-bottom: 1px solid white;
    margin: 0 auto;
    margin-bottom: 10px;
    font-family: 'Roboto Condensed', sans-serif;
    display: grid;
    grid-template-columns: 105px 195px;
    padding: 5px 25px 5px 5px;
    & p{
        font-family: 'Roboto Condensed', sans-serif;
        color: white; 
        text-align: right;
        padding-right: 10px;
        // font-weight: 900;
        font-size: 18px;
    }
    & span{
        font-size: 15px;
        color: white; 
        font-weight: 200;
        padding-top: 4px;
        padding-left: 5px;
        align-self: center;
        display: block;
    }
    & h3{
        font-size: 18px;
        color: white; 
        font-weight: 600;
        padding: 0 5px 2px;
        align-self: center;
        display: block;
        border-bottom: 1px solid white;
        width: fit-content;
    }
}
.itemImg{
    min-height: 170px;
    display: flex;
    height: 100%;
    & img{
        // height: 150px;
        width: 95%;
        margin: 0 auto;        
        display: flex;
        align-self: flex-end;
        border-radius: 10px;
        border: 1px solid black;
    
    }
}
